import {useState,useEffect} from 'react'
import {ListGroup, ListGroupItem} from 'reactstrap'
import Axios from 'axios'

const Repos = ({repo_url}) => {
    const[repo,setRepo] = useState([])

    const fetchRepo = async () => {
        const {data} = await Axios.get(repo_url)
        const details = data;
        setRepo(details)
        console.log('repo data',repo);
    }

    useEffect(() => {
     fetchRepo()   
    },[repo_url])

    return ( 
        <ListGroup>
            {repo.map(item => (                <ListGroupItem key={item.id}>
                    <div className="text-primary">{item.name}</div>
                    <div className="text-secondary">{item.language}</div>
                    <div className="text-info">{item.description}</div> 
                    <div className="text-link"><a href={item.html_url} target="__blank">Repo Link</a></div> 
                </ListGroupItem>
            )) }
        </ListGroup>
     );
}
 
export default Repos;