import React, { useState, useContext } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  NavbarText
} from "reactstrap";

import { Link } from "react-router-dom";

import { userContext } from "../Context/userContext";

const Header = () => {

    const context = useContext(userContext)
    const [isOpen,setIsOpen] = useState(false)

    const toggle = () => {
        setIsOpen(!isOpen)
    }

  return (
    <Navbar color="info" light expand="md"
    className='px-2'>
      <NavbarBrand>
        <Link to="/" className="text-white"
        style={{
            textDecoration:'none',
            marginLeft:'16px'
        }} >
          Github Repo App
        </Link>
      </NavbarBrand>
      <NavbarText className='text-white'> {context.user?.email ? `Welcome ${context.user.email}` : ""}</NavbarText>
      <NavbarToggler onClick={toggle} />
      <Collapse isOpen={isOpen} navbar>
        <Nav className="ms-auto mr-2" navbar
        style={{paddingRight:'16px'}}>
            {context.user ? (<NavItem>
            <NavLink className="text-white" onClick= {() => context.setUser(null)} >Logout</NavLink>
          </NavItem>) :
           (
               <>
               <NavItem>
               <NavLink className="text-white" tag={Link} to='/signup' >Signup</NavLink>
               </NavItem>
               <NavItem>
               <NavLink className="text-white" tag={Link} to='/signin' >Signin</NavLink>
               </NavItem>
            </>
           ) }
        </Nav>
      </Collapse>
    </Navbar>
  );
};

export default Header;
