import {Card, CardBody} from 'reactstrap'

const UserCard = ({user}) => {
    return ( 
        <Card className="text-center mt-2 mb-5" style={{ width:'350px'}}>
            <img src={user.avatar_url} alt="" className=" img-thumbnail border-primary" height="300px" width='350px'/>
            <CardBody>
                <div className="text-rimary">{user.name}</div>
                <div className="text-rimary">{user.location}</div>
                <div className="text-rimary">{user.bio}</div>
                <div className="text-rimary">Available for hire: {user.hireable ? 'Yes':'No'}</div>
                <div className="text-rimary">Followers {user.followers}</div>
            </CardBody>
        </Card>
    );
}

export default UserCard;